



























import { mapState, mapMutations } from 'vuex'
export default {
	data() {
		return {
			maxSize: 1000000 // 图片压缩临界值
		}
	},
	computed: {
		...mapState(['imagearr', 'showImage', 'qiniudomain'])
	},
	methods: {
		...mapMutations(['SHOAIMAGE', 'DELIMAGE']),
		delImage(name) {
			this.DELIMAGE(name);
		},
		uploadPic() {
			let that = this;
			wx.chooseImage({
				count: 16 - that.showImage.length,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: function(res) {
					console.log(res);
					res.tempFiles.forEach(v => {
						// 存放临时显示文件
						that.imgCompress(v);
					})
				}
			})
		},
		 imgCompress(img) {
			 let that = this;
			//用于压缩图片的canvas
			let ctx = wx.createCanvasContext('photo_canvas');
			
			let sz = img.size;
			let wd = 0;
			let ht = 0;
			wx.getImageInfo({
				src: img.path,
				success: (g) => {
					wd = g.width;
					ht = g.height;
					// 大于100万像素则压缩之后上传
					if(sz > that.maxSize){
						let line = ht > wd? ht: wd;
						let ratio = line / 1000;
						
						ctx.drawImage(img.path, 0, 0, wd / ratio, ht / ratio);
						ctx.draw(false, () => {
							that.canvasToPath(wd / ratio, ht / ratio);
						});
					}else{
						that.SHOAIMAGE(img.path);
						console.log('compress image');
						that.$store.dispatch('uploadQiniu', { tempFilePaths: img.path });
					}	
				}
			})
		},
		canvasToPath(w, h) {
			let that = this;
			wx.canvasToTempFilePath({
				width: w,
				height: h,
				destWidth: w,
				destHeight: h,
			  	canvasId: 'photo_canvas',
				quality: 0.8,
				fileType: 'jpg',
			  	success: (r) => {
					let res = r.tempFilePath;

					that.SHOAIMAGE(res); 
					console.log('original image');
					
					that.$store.dispatch('uploadQiniu', { tempFilePaths: res });
				}
			})
		},
		toCancel() {
			this.$emit('cancelUpload');
		}
	}
}



