















export default {
	data() {
		return {
			kword: ''
		}
	},
	methods: {
		toSearch() {
			let kwd = this.kword.trim();
			
			this.$emit('toSearch', kwd);
			this.$emit('cancelSearch');
		},
		toCancel() {
			this.$emit('cancelSearch');
		}
	}
}



