

















export default {
	props: ['sellId'],
	data() {
		return {
			price: ''
		}
	},
	methods: {
		toCancel() {
			this.$emit('cancelConfirm');
		},
		toSubmit() {
			if(!this.price.trim()){
				wx.showToast({
					title: '请输入成交价格',
					icon: 'none'
				})
				return;
			}
			
			this.$store.dispatch('setSell', {id: this.sellId, price: parseFloat(this.price)});
			
			this.$emit('cancelConfirm');
		}
	}
}



