














export default {
	data() {
		return {
			searchshow: false,
			keyword: ''
		} 
	}, 
	methods: {
		toSearch(k) {
			this.keyword = k;
			this.$emit('keywordSearch', k);
		},
		toShowHide(k) {
			// this.shadow = false;
			this.searchshow = true;
		},
		cancelSearch() {
			this.searchshow = false;
		},
		resetSearch() {
			this.searchshow = false;
			this.keyword = '';
		}
	}
}



