











export default {
	props: ['name', 'placeholder', 'type', 'defaultData'],
	data() {
		return {
			date: ''
		}
	},
	onReady() {
		if(this.defaultData){
			this.date = this.defaultData;
		}
	},
	watch: {
		defaultData(n, o) {
			if(n){
				this.date = n;
			}
		}
	},
	computed: {
		info() {
			return this.date ? this.date : `请选择${this.placeholder?this.placeholder:this.name}`
		},
		activeColor() {
			return this.date ? '#000000' : '#999999'
		}
	},
	methods: {
		dateChange(r) {
			console.log(r);
			this.date = r.detail.value;
			this.$emit('listChange', this.date, this.type);
		},
		compReset() {
			this.date = '';
		}
	}
	
}



