










let inputrun;
export default {
	props: ['name', 'unit', 'placeholder', 'type', 'defaultData', 'readonly'],
	data() {
		return {
			state: ''
		}
	},
	onReady() {
		if(this.defaultData){
			this.state = this.defaultData;
		}
	},
	computed: {
		fontSize() {
			let size = 0;
			switch(this.name.length) {
				case 4:
				case 3:
				case 2:
					size = 26;
				break;
				case 5:
					size = 20;
				break;
			}
			return size;
		},
	},
	watch: {
		defaultData(n, o) {
			if(n){
				this.state = n;
			}
		}
	},
	methods: {
		compReset() {
			this.state = '';
		},
		inputInfo(i) {
			let vl = i.detail.value;
			
			clearTimeout(inputrun);
			inputrun = setTimeout(() => {
				this.$emit('inputChange', vl, this.type);
			}, 300)
		}
	}
}



