





















import { mapState } from 'vuex';
export default {
	data() {
		return {
			sellConfirm: false,
			sellId: ''
		}
	},
	computed: {
		...mapState(['owncar'])
	},
	methods: {
		cancelConfirm() {
			this.sellConfirm = false;
		},
		showCtrl(id) {
			let that = this;
			wx.showActionSheet({
				itemList: ['编辑', '成交', '删除', '合同签订'],
				success: (res) => {
					switch(res.tapIndex) {
						// 0编辑 1成交 2删除
						case 0:
							wx.navigateTo({
								url: '/pages/public/public?id=' + id
							})
						break;
						case 1:
							that.sellId = id;
							that.sellConfirm = true;
						break;
						case 2:
							wx.showModal({
								title: '删除库存车辆',
								content: '是否删除该车辆？',
								success: r => {
									if(r.confirm){
										that.$store.dispatch('delOwncar', id);
									}
								}
							}) 
						break;
						case 3:
							wx.navigateTo({
								url: '/pages/contract/contract?carid=' + id
							})
						break;
					}
					console.log(res);
				}
			});
		}
	}
}



